.register-wrapper {
  background-image: linear-gradient(135deg, #0eeff0 30%, #088f90 90%);
  position: relative;
  z-index: 3;
  padding: 100px;
  font-size: 1.2rem;
  color: #999999; }
  .register-wrapper * {
    box-sizing: border-box; }
  .register-wrapper .register-title {
    font-size: 2rem;
    padding-bottom: 20px;
    margin: 0 0 20px;
    border-bottom: dashed 1px #e6e6e6; }
  .register-wrapper .register-block {
    position: relative;
    display: block;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
    padding: 30px;
    background: #fff;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 5px; }
    .register-wrapper .register-block::after {
      transition-delay: 0.2s; }
    .register-wrapper .register-block::before, .register-wrapper .register-block::after {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: block;
      width: 100%;
      height: 100%;
      content: "";
      background-color: white;
      border: 1px solid #e6e6e6;
      border-radius: 5px;
      transform: scale(0.9) translate(0, 0);
      transition: all 0.3s ease; }
    .register-wrapper .register-block:hover::before {
      z-index: -1;
      transform: scale(0.96) translate(0, 16px); }
    .register-wrapper .register-block:hover::after {
      z-index: -2;
      transform: scale(0.92) translate(0, 32px); }
  .register-wrapper form input {
    display: inline-block;
    width: 100%;
    padding: 0 20px;
    line-height: 50px;
    background: white;
    border: solid 1px #e6e6e6;
    border-radius: 5px;
    transition: all 0.3s ease;
    outline: none; }
    .register-wrapper form input:not(:last-child) {
      margin-bottom: 20px; }
    .register-wrapper form input[type="email"]:focus, .register-wrapper form input[type="email"]:active, .register-wrapper form input[type="password"]:focus, .register-wrapper form input[type="password"]:active {
      border-color: #ccc; }
    .register-wrapper form input[type="submit"] {
      position: relative;
      z-index: 1;
      display: inline-block;
      padding: 0 20px;
      background-color: #f2395a;
      background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0));
      background-size: 20px 20px, 100% 100%;
      border: 1px solid #f02146;
      border-bottom-width: 3px;
      color: white;
      outline: none;
      cursor: pointer;
      transition: all 0.3s ease; }
      .register-wrapper form input[type="submit"]:hover {
        background-size: 40px 40px; }
